<template>
	<div class="container">
		<v-back :title="$route.query.type == 'set' ? '师生信息修改' : '添加师生'" />
		<div class="scroll-view">
			<md-field>
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">身份</label>
						<div class="md-field-item-control">
							<md-radio-group v-model="usertype">
								<md-radio-box name="S" style="margin-right: 5px;" :disabled="$route.query.type == 'set'">学生</md-radio-box>
								<md-radio-box name="T" style="margin-right: 5px;" :disabled="$route.query.type == 'set'">老师</md-radio-box>
								<md-radio-box name="T1" :disabled="$route.query.type == 'set'">辅导员</md-radio-box>
							</md-radio-group>
						</div>
					</div>
				</div>
				
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">* 图片</label>
						<div class="md-field-item-control">
							<ul class="image-reader-list">
								<li
								class="image-reader-item"
								v-for="(img, index) in imageList['tupian']"
								:key="index"
								:style="{
									'backgroundImage': `url(${img})`,
									'backgroundPosition': 'center center',
									'backgroundRepeat': 'no-repeat',
									'backgroundSize': 'cover'
								}"
								@click="imgsShow(img)">
								<md-tag
									class="image-reader-item-del"
									size="small"
									shape="quarter"
									fill-color="#111A34"
									type="fill"
									font-color="#fff"
									@click.native.stop="onDeleteImage('tupian', index)"
								>
									<md-icon name="close"></md-icon>
								</md-tag>
								</li>
								<li class="image-reader-item add" v-show="imageList.tupian.length == 0">
									<md-image-reader
										name="tupian"
										@select="onReaderSelect"
										@complete="onReaderComplete"
										@error="onReaderError"
									>
									</md-image-reader>
									<md-icon name="camera" size="md" color="#CCC"></md-icon>
									<p>添加图片</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				
				<md-input-item
				  ref="input0"
				  title="* 姓名"
				  placeholder="请输入姓名"
				  is-amount
				  :maxlength="15"
				  v-model="username">
				</md-input-item>
				
				<md-input-item
				  ref="input0"
				  :disabled="$route.query.type == 'set'"
				  title="* 学号/工号"
				  placeholder="请输入学号/工号"
				  is-amount
				  :maxlength="15"
				  v-model="usernbr" >
				</md-input-item>
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">性别</label>
						<div class="md-field-item-control">
							<md-radio-group v-model="sex">
								<md-radio-box name="1" style="margin-right: 5px;">男</md-radio-box>
								<md-radio-box name="0">女</md-radio-box>
							</md-radio-group>
						</div>
					</div>
				</div>
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 学院"
				        :content="college"
				        @click="showcollege"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="collegeShow"
				      default-value="2"
				      :data="collegedata"
				      max-height="320px"
				      title="学院选择"
				      large-radius
				      @choose="collegeChoose"
				    >
				</md-selector>
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 专业"
				        :content="org"
				        @click="showorg"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="orgShow"
				      default-value="2"
				      :data="orgdata"
				      max-height="320px"
				      title="专业选择"
				      large-radius
				      @choose="orgChoose"
				    >
				</md-selector>
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 班级"
				        :content="classs"
				        @click="showclass"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="classShow"
				      default-value="2"
				      :data="classdata"
				      max-height="320px"
				      title="班级选择"
				      large-radius
				      @choose="classChoose"
				    >
				</md-selector>
				
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 楼栋"
				        :content="building"
				        @click="showbuilding"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="buildingShow"
				      default-value="2"
				      :data="buildingdata"
				      max-height="320px"
				      title="楼栋选择"
				      large-radius
				      @choose="buildingChoose"
				    >
				</md-selector>
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 宿舍号"
				        :content="dorm"
				        @click="showdorm"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="dormShow"
				      default-value="2"
				      :data="dormdata"
				      max-height="320px"
				      title="宿舍号选择"
				      large-radius
				      @choose="dormChoose"
				    >
				</md-selector>
				
				<md-field style="padding: 0;" v-show="usertype == 'S'">
					<md-field-item
				        title="* 床位号"
				        :content="bed"
				        @click="showbed"
				        arrow
				        solid
					/>
				</md-field>
				<md-selector
				      v-model="bedShow"
				      default-value="2"
				      :data="beddata"
				      max-height="320px"
				      title="床位号选择"
				      large-radius
				      @choose="bedChoose"
				    >
					<template slot-scope="{ option, index, selected }">
						<div :style="selected?'color: #2f86f6;':''">
							{{option.text}}号
							<span>{{option.disabled ? '已被使用' : '可选'}}</span>
						</div>
					</template>
				</md-selector>
				
				<div class="md-field-item is-solid" v-show="usertype == 'T' || usertype == 'T1'">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">* 访问楼栋</label>
						<div class="md-field-item-control">
							<div class="md-example-child md-example-child-drop-menu md-example-child-drop-menu-0">
								<md-check-group v-model="insurants">
								  <md-check-box :name="item.buildingId" v-for="(item, index) in floors" :key="index">{{item.buildingName}}</md-check-box>
								</md-check-group>
							</div>
						</div>
					</div>
				</div>
				
				<md-input-item
				  ref="input2"
				  title="身份证号"
				  placeholder="18个字符"
				  is-amount
				  :minlength="18"
				  :maxlength="18"
				  v-model="id_card">
				</md-input-item>
				
				
				<md-input-item
				  ref="input2"
				  title="邮箱"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="email">
				</md-input-item>
				
				<md-input-item
				  ref="input2"
				  title="手机号"
				  placeholder="11个字符"
				  is-amount
				  :maxlength="11"
				  v-model="mobile">
				</md-input-item>
				
				<div style="font-size: 3.5vw;padding: 10px;">注意：新添加的老师或学生默认登录密码123456</div>
			</md-field>
			<md-field>
				<md-button type="primary" size="small" round @click="submit">提交保存</md-button>
			</md-field>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import axios from 'axios'
	import compressImage from 'ls-compressimage'
	export default {
		name: 'add_yellow',
		data() {
			return {
				isViewerShow: false,
				imgs: [],
				id: '',
				userId: '',
				usertype: 'S',
				old_bed_id: '',
				username: '',//姓名
				usernbr: '',
				sex: '1',
				id_card: '',
				email: '',
				mobile: '',//联系方式
				imageList: {
				    tupian: []
				},
				blob: null,
				file: {},
				
				
				building: '',
				building_id: '',
				buildingdata: [],
				buildingShow: false,
				
				dorm: '',
				dorm_id: '',
				dormdata: [],
				dormShow: false,
				
				bed: '',
				bed_id: '',
				beddata: [],
				bedShow: false,
				
				insurants: [],
				floors: [],
				
				college: '',
				college_id: '',
				collegedata: [],
				collegeShow: false,
				
				org: '',
				org_id: '',
				orgdata: [],
				orgShow: false,
				
				classs: '',
				class_id: '',
				classdata: [],
				classShow: false,
			}
		},
		methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			showcollege() {
				this.collegeShow = true
			},
			collegeChoose(item) {
				if (this.college_id != item.value) {
					this.college = item.text
					this.college_id = item.value
					this.getDepartment(item.value, 'org')
					this.org = ''
					this.org_id = ''
				}
			},
			showorg() {
				this.orgShow = true
			},
			orgChoose(item) {
				if (this.org_id != item.value) {
					this.org = item.text
					this.org_id = item.value
					this.getDepartment(item.value, 'class')
					this.classs = ''
					this.class_id = ''
				}
			},
			showclass() {
				this.classShow = true
			},
			classChoose(item) {
				if (this.class_id != item.value) {
					this.classs = item.text
					this.class_id = item.value
				}
			},
			showbuilding() {
				this.buildingShow = true
			},
			buildingChoose(item) {
				if (this.building_id != item.value) {
					this.building = item.text
					this.building_id = item.value
					this.getDorms()
					this.dorm = ''
					this.dorm_id = ''
					this.bed = ''
					this.bed_id = ''
				}
			},
			showdorm() {
				this.dormShow = true
			},
			dormChoose(item) {
				if (this.dorm_id != item.value) {
					this.dorm = item.text
					this.dorm_id = item.value
					this.getBeds()
					this.bed = ''
					this.bed_id = ''
				}
			},
			showbed() {
				this.bedShow = true
			},
			bedChoose(item) {
				this.bed = item.text
				this.bed_id = item.value
			},
			
			
			onReaderSelect(name, {files}) {
			    files.forEach(file => {
			        console.log('[Mand Mobile] ImageReader Selected:', 'File Name ' + file.name)
			    })
			    this.$toast.loading('图片读取中...')
			},
			onReaderComplete(name, {dataUrl, blob, file}) {
			    this.$toast.hide()
			    console.log('[Mand Mobile] ImageReader Complete:', 'File Name ' + file.name)
			    setTimeout(() => {
			        const demoImageList = this.imageList[name] || []
			        demoImageList.push(dataUrl)
			        this.$set(this.imageList, name, demoImageList)
			        this.blob = blob
			        this.file = file
			    }, 100)
			},
			onReaderError(name, {msg}) {
			    this.$toast.failed(msg)
			},
			onDeleteImage(name, index) {
			    const demoImageList = this.imageList[name] || []
			    demoImageList.splice(index, 1)
			    this.$set(this.imageList, name, demoImageList)
			},
			submit() {
				let params = {
					username: this.username,
					usertype: this.usertype,
					usernbr: this.usernbr,
					sex: this.sex,
					id_card: this.id_card,
					email: this.email,
					mobile: this.mobile,
					building_id: this.building_id,
					dorm_id: this.dorm_id,
					bed_id: this.bed_id,
					building_name: this.building,
					dorm_nbr: this.dorm,
					bed_nbr: this.bed,
					
					college_id: this.college_id,
					org_ids: this.org_id,
					class_ids: this.class_id,
					college_name: this.college,
					org_names: this.org,
					class_names: this.classs,
					
					photo: '',
					arr: JSON.stringify(this.insurants) || []
				}
				
				if (this.username == '') {
					this.$toast.failed('请输入姓名')
				} else if (this.imageList.tupian.length == 0) {
					this.$toast.failed('请上传图片')
				} else if (this.usernbr == '') {
					this.$toast.failed('请输入学号/工号')
				} else {
					if ((this.usertype == 'T' || this.usertype == 'T1') && this.insurants.length == 0) {
						this.$toast.failed('请选择可访问楼栋')
						return;
					}
					if (this.usertype == 'S' && this.college_id == '') {
						this.$toast.failed('请选择学院')
						return
					}
					if (this.usertype == 'S' && this.org_id == '') {
						this.$toast.failed('请选择专业')
						return
					}
					if (this.usertype == 'S' && this.class_id == '') {
						this.$toast.failed('请选择班级')
						return
					}
					if (this.usertype == 'S' && this.building_id == '') {
						this.$toast.failed('请选择楼栋')
						return;
					}
					if (this.usertype == 'S' && this.dorm_id == '') {
						this.$toast.failed('请选择宿舍')
						return;
					}
					if (this.usertype == 'S' && this.bed_id == '') {
						this.$toast.failed('请选择床位')
						return
					}
					if (this.blob == null) {
						params.photo = this.imageList.tupian[0]
						this.updateStudent(params);
					} else { 
						if (this.usertype == 'S') {
							if (this.$route.query.type == 'set') {
								this.uploadImg(params, 'student')
							} else {
								this.usernbr_isnull(() => {
									this.uploadImg(params, 'student')
								})
							}
						} else {
							if (this.$route.query.type == 'set') {
								this.uploadImg(params, 'teacher')
							} else {
								this.usernbr_isundefind(() => {
									this.uploadImg(params, 'teacher')
								})
							}
						}
					}
				}
			},
			uploadImg(params, usertype) {
				this.$toast.loading('图片上传中...');
				new compressImage({
					res: this.file, // 必选 资源
					justConversion: false, // 可选 声明了此字段说明只进行格式转换 res requestType responseType callback必填，其他随意
					width: 300, // 可选(justConversion存在可不选) (默认图片自身宽度,建议自定义) 压缩后图片宽度
					//height: 300,//可选 (默认 自定义图片宽度/原生图片宽度*原生图片高度) 压缩后图片高度
					quality: 0.1, // 可选 (默认0.3) 压缩层级质量（0<quality<1）越大压缩越快 图片质量跨度相对低 越小则相反
					size: 500, // 必选(justConversion存在可不选) 压缩后图片大小 单位k
					requestType: 'file', // 必选 传入文件类别 base64 | file | blob
					responseType: 'file', // 必选 返回文件类别 base64 | file | blob
					fileName: 'student', // 如果responseType的类型为file和非justConversion 此选项可选 返回文件对象的文件名
					callback: (result) => {
					  if (result === null) {
						this.$toast.failed("图片上传失败")
						return
					  }
					  var formData = new FormData();
					  formData.append("file", result, new Date().getTime() + '.' + result.type.split('/')[1]);
					  if (usertype == 'student') {
						  axios.post('http://106.12.3.212/system/bstudent/UploadStudentImg', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
							  if (res.data.code == 0) {
								  params.photo = res.data.url
								  setTimeout(() => {
									this.updateStudent(params)
								  }, 500)
							  } else {
								  this.$toast.failed("图片上传失败")
							  }
						  }).catch((err) => {
								this.$toast.failed("图片上传失败")
						  })
					  } else {
						  axios.post('http://106.12.3.212/system/bteacher/UploadTeacherImg', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
							  if (res.data.code == 0) {
								  params.photo = res.data.url
								  setTimeout(() => {
									this.updateStudent(params)
								  }, 500)
							  } else {
								  this.$toast.failed("图片上传失败")
							  }
						  }).catch((err) => {
								this.$toast.failed("图片上传失败")
						  })
					  }
					  
					  
					  /* axios.post(this.$baseUrl() + '/users/upload/' + usertype, formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
					  	console.log(res.data.data)
						params.photo = res.data.data
						setTimeout(() => {
							this.updateStudent(params)
						}, 500)
					  }).catch((err) => {
					  	this.$toast.failed("图片上传失败")
					  }) */
					} // 回调函数 返回处理后文件 如果为null 表示文件处理不了(原因 少了参数 参数不合法))
				})
			},
			//新增的时候判断学号是否存在
			usernbr_isnull(callback) {
				this.$get('/users/b_student', {
					usernbr: this.usernbr
				}, (data) => {
					if (callback) callback();
				})
			},
			//新增的时候判断工号是否存在
			usernbr_isundefind(callback) {
				this.$get('/users/b_teacher', {
					usernbr: this.usernbr
				}, (data) => {
					if (callback) callback();
				})
			},
			updateStudent(params) {
				if (this.$route.query.type == 'set') {
					params.id = this.id
				}
				this.$post('/users/add_update_user', params, ()=>{
				    this.$toast.succeed("提交成功")
					setTimeout(() => {
						this.$router.back()
					}, 1000)
				})
			},
			//获取宿舍号
			getDorms() {
				this.$get('/dorm/hostel', {
					buildingId: this.building_id
				}, (data) => {
					let list = data.data
					let arr = []
					list.forEach(item => {
						arr.push({
							value: item.dormId,
							text: item.dormNbr
						})
					})
					this.dormdata = arr
				})
			},
			//获取空闲床位号
			getBeds() {
				if (this.dorm_id) {
					this.$get('/dorm/bed', {
						dormId: this.dorm_id
					}, (data) => {
						let list = data.data
						let arr = []
						list.forEach(item => {
							arr.push({
								value: item.bed_id,
								text: item.bed_nbr,
								disabled: item.status != '0' && item.bed_id != this.old_bed_id
							})
						})
						this.beddata = arr
					})
				}
			},
			//获取院系/专业/班级
			getDepartment(parent_org_id, type) {
				if (type == 'class') {
					this.$get('/college/class', {
						org_id: parent_org_id
					}, (data) => {
						let list = data.data
						let arr = []
						list.forEach(item => {
							arr.push({
								value: item.id,
								text: item.bj_name
							})
						})
						this.classdata = arr
					})
					return;
				}
				this.$get('/college/department', {
					parent_org_id: parent_org_id
				}, (data) => {
					let list = data.data
					let arr = []
					list.forEach(item => {
						arr.push({
							value: item.orgId,
							text: item.orgName
						})
					})
					if (type == 'college') {
						this.collegedata = arr
					}
					if (type == 'org') {
						this.orgdata = arr
					}
					if (type == 'class') {
						this.classdata = arr
					}
				})
			}
		},
		mounted() {
			//获取楼栋
			this.$get('/dorm/floor', {
				//
			}, (data) => {
				let list = data.data
				let arr = []
				list.forEach(item => {
					arr.push({
						value: item.buildingId,
						text: item.buildingName
					})
				})
				this.buildingdata = arr
				this.floors = data.data
			})
			this.getDepartment(21082, 'college')
			if (this.$route.query.type == 'set') {
				document.title = window.configs.headertitle + '师生信息修改'
				this.usertype = this.$route.query.usertype
				this.$get('/users/person_ts', {
					usernbr: this.$route.query.usernbr
				}, (data)=>{
					let user = data.data[0]
					this.id = user.user_id
					this.old_bed_id = user.bed_id
					this.username = user.username
					this.usernbr = user.usernbr
					this.sex = user.sex
					this.id_card = user.id_card
					this.email = user.email
					this.mobile = user.mobile
					this.building_id = user.building_id
					this.dorm_id = user.dorm_id
					this.bed_id = user.bed_id
					this.building = user.building_name
					this.dorm = user.dorm_nbr
					this.bed = user.bed_nbr
					this.college_id = user.college_id
					this.class_id = user.class_ids
					this.org_id = user.org_ids
					this.college = user.college_name
					this.org = user.org_names
					this.classs = user.class_names
					this.insurants = []
					this.imageList.tupian = user.photo ? [user.photo] : [],
					console.log(this.imageList.tupian)
					user.person_entry_exit_rights.forEach(item => {
						this.insurants.push(item.entry_exit_building_id)
					})
					
					if (this.$route.query.usertype == 'S') {
						this.getDorms()
						this.getBeds()
						this.getDepartment(this.college_id, 'org')
						this.getDepartment(this.org_id, 'class')
					}
				})
			}
		}
	}
</script>

